





































































































  import { Vue, Component, Prop, Watch } from "vue-property-decorator";
  import WalletMoneroHandlerModule from "@/store/modules/WalletMoneroHandlerModule";

  import DashboardContent from "@/components/Layout/DashboardContent.vue";
  import DashboardTitle from "@/components/Text/DashboardTitle.vue";
  import DashboardSubtitle from "@/components/Text/DashboardSubtitle.vue";
  import Input from "@/components/Form/Input.vue";
  import Label from "@/components/Form/Label.vue";
  import Screen from "@/components/Layout/Screen.vue";
  import Footer from "@/components/Layout/Footer.vue";
  import ButtonPrimary from "@/components/Buttons/ButtonPrimary.vue";
  import BigNumber from "bignumber.js";
  

  /* eslint-disable no-unused-vars */
  
import Language from "@/lang/langInterface";

  
  /* eslint-enable no-unused-vars */
  
  @Component({components: { DashboardTitle, DashboardSubtitle, DashboardContent, Input, Label, Screen, Footer, ButtonPrimary }})
  export default class SendMonero extends Vue {
    
    private address = "";
    private xmrAmount = ''
    private fiatAmount = ''
    private selectedFeeRate = 1;
    private addressError = false;
    private sendMaxFlag = false;
  
    @Prop() currency!: string;
    @Prop() language!: Language;
    @Prop() lastTransaction! : string;
  
    showModalMonero() {
      this.$emit("show-amount-modal");
    }
  
    setSelectedFeeRate(r: number) {
      this.selectedFeeRate = r;
    }

    
  calculateXmr() {
  
      if(this.fiatAmount == '') {
        this.xmrAmount = ''
        return
      }
  
      let fiat = this.fiatAmount;
  
      // Determine the format
      let format = new Intl.NumberFormat(WalletMoneroHandlerModule.settings.language).format(1.5);
  
      // Are we in commas as decimal land?
      let commasAsDecimal = format == "1,5";
  
      // Let's adjust our format
      if (commasAsDecimal) {
        fiat = fiat.replace(".", "");
        fiat = fiat.replace(",", ".");
      }
  
      this.xmrAmount = (
        parseFloat(fiat.replace(/,/g, "")) / WalletMoneroHandlerModule.fiatRate
      ).toFixed(8).replace(/(?:\.0+|(\.\d+?)0+)$/, "$1");
  
      if (isNaN(parseFloat(this.xmrAmount))) {
        this.xmrAmount = "0";
      }
  
  }
  
  get sendDisabled() {
    return this.addressError || this.address == '' || this.notEnoughBalance || this.isSendingdust || isNaN(parseFloat(this.xmrAmount)) || parseFloat(this.xmrAmount) <= 0
  }
  
  get isSendingdust() {
    if(this.xmrAmount != '' && parseFloat(this.xmrAmount) > 0 && !isNaN(parseFloat(this.xmrAmount))) {
      return new BigNumber(this.xmrAmount).multipliedBy(100000000).toNumber() <= 800
    }
    else {
      return false
    }
  }

  

  
  get notEnoughBalance() {
    const ammount_to_compare = new BigNumber(this.xmrAmount).multipliedBy(1000000000000);
    return ammount_to_compare.gte(WalletMoneroHandlerModule.unlockedBalance)
  }
  
  get sendingMax() {
    return new BigNumber(this.xmrAmount).eq(WalletMoneroHandlerModule.unlockedBalance)
  }
  

  
  
  

  
  
  @Watch('lastTransaction')
    clearFields() {
      this.xmrAmount = ''
      this.address = ''
      this.setSelectedFeeRate(1)
    }
  
  @Watch('currency') 
    currencyChanged() {
      this.calculateFiat()
    }
  
    @Watch("address")
     verifyAddress() {
      this.address = this.address.trim();
      this.addressError = false;
      if (this.address.trim() === "") {
        this.addressError = true;
        return;
      }

      
      //mainnet regex
      let regex = /^4[0-9AB][1-9A-HJ-NP-Za-km-z]{93}$/;

      //stage regex
      // const regex = /^5[123456789ABCDEFGHJKLMNPQRSTUVWXYZabcdefghijkmnopqrstuvwxyz]{94}$/;
      
      if (regex.test(this.address)) {
        this.addressError = false;
      } else {
        this.addressError = true;
      }
    }


    
  
    async calculateFiat() {
  
      if(this.xmrAmount == '') {
        this.fiatAmount = ''
        return
      }
  

      let fiat = (parseFloat(this.xmrAmount) * WalletMoneroHandlerModule.fiatRate).toFixed(2);
  
      if (isNaN(parseFloat(fiat))) {
        fiat = "0"
      }
  
       this.fiatAmount = new Intl.NumberFormat(WalletMoneroHandlerModule.settings.language).format(parseFloat(fiat))
  
    }
  
    sendMax() {
      const tempMax = (new BigNumber(WalletMoneroHandlerModule.balance)).dividedBy(1000000000000).toFixed(8);
      const tempSub = parseFloat(tempMax)- 0.001;
      this.xmrAmount = tempSub.toString();
      this.sendMaxFlag = true
      this.calculateFiat()
    }
  
  
    async sendButtonClicked() {
  
      let priority = this.selectedFeeRate;

      let amount_to_send = new BigNumber(this.xmrAmount).multipliedBy(1000000000000);
      this.address = this.address.trim();

      try {
        
        const psbt = await WalletMoneroHandlerModule.wallet.createTransaction(this.sendingMax, this.address, amount_to_send.toString(), priority)
        this.$emit("show-transaction-confirm", [psbt, this.address, this.sendingMax, this.xmrAmount]);
      } catch(e) {
        this.$emit("show-error-xmr", e);

      }
      finally {
        this.sendMaxFlag = false
        this.xmrAmount = '';
        this.address = '';
        this.fiatAmount = '';
      }
    }
  }
  