













































declare global {
    interface Window {
        ipcRenderer:any;
    }
}


import { Vue, Prop, Component, Watch } from 'vue-property-decorator';
import BigNumber from "bignumber.js";
import moment from 'moment'
import 'moment/locale/es';
import 'moment/locale/ca';
import 'moment/locale/fr';
import 'moment/locale/ja';
import 'moment/locale/pt-br';

/* eslint-disable no-unused-vars */
import Language from "@/lang/langInterface";
import { MoneroTransaction } from '@/wallet/moneroWallet'
import WalletMoneroHandlerModule from '@/store/modules/WalletMoneroHandlerModule';
/* eslint-enable no-unused-vars */

@Component
export default class OverviewTable extends Vue {

@Prop() language!: Language;
@Prop() transactionsMonero! : MoneroTransaction[];
private module = WalletMoneroHandlerModule
private zero = new BigNumber(0);
private dateHandler = moment

  getText(tx: any, translate = true) {
    if(tx.isIncoming) {
      return translate ? this.language.incoming_monero : 'Incoming';
    }
    if(tx.isOutgoing) {
      return translate ? this.language.outgoung_monero : 'Outgoing';
    }    
  }

  created() {
    this.setMomentLocale()
  }

  openTransactionView(link : string) {
    window.ipcRenderer.send('openLink', link)
  }

  @Watch('language')
  setMomentLocale() {
    if(this.module.settings.language != "en") {
      moment.locale(this.module.settings.language)
    }
    else {
      moment.locale('en-GB')
    }
  }

}

