






























































































































































































































































































































































































































































































import { Component, Vue, Prop } from "vue-property-decorator";
import PageTitle from "@/components/Text/PageTitle.vue";
import PageSubtitle from "@/components/Text/PageSubtitle.vue";
import OL from "@/components/List/OL.vue";
import LI from "@/components/List/LI.vue";
import OverView from "@/components/Overview.vue";
import Checkbox from "@/components/Form/Checkbox.vue";
import BackLink from '@/components/Buttons/BackLink.vue'
import ButtonPrimary from '@/components/Buttons/ButtonPrimary.vue'
import Receive from "@/components/Receive.vue";
import ReceiveMonero from "@/components/ReceiveMonero.vue";
import Send from "@/components/Send.vue";
import SendMonero from "@/components/SendMonero.vue";
import Swap from "@/components/Swap.vue";
import Settings from "@/components/Settings.vue";
import WalletHandlerModule from "@/store/modules/WalletHandlerModule";
import WalletMoneroHandlerModule from "@/store/modules/WalletMoneroHandlerModule";
import BigNumber from "bignumber.js";
import { Transaction } from '@/wallet/wallet';
import { moneroWalletDB } from '@/wallet/moneroWallet';

/* eslint-disable no-unused-vars */

import Language from "@/lang/langInterface";
import { parse } from "path";

/* eslint-enable no-unused-vars */

@Component({ components: { OverView, Receive, ReceiveMonero, Send, SendMonero, Swap, Settings, BackLink, ButtonPrimary, PageTitle, PageSubtitle, Checkbox, OL, LI } })
export default class WalletHomeView extends Vue {
  private currentTab = "Overview";

  // Seed modal
  private seedModal = false;

  private seedModalMonero = false;  

  // Delete modal
  private deleteModal = false

  private isMac = WalletHandlerModule.isMac

  // Transaction modal
  private transactionModalShowing = false;

  private errorModalShowing = false;
  private transactionModalMoneroShowing = false;

  // Our refresh handler
  private timer : null | NodeJS.Timeout = null;

  // Transactions with >=6 confirmations
  private transactions = WalletHandlerModule.transactions;
  private transactionsMonero = WalletMoneroHandlerModule.transactions;

  // Unconfirmed transactions
  private unconfirmedTransactions = WalletHandlerModule.unconfirmedTransactions;
  
  private balance = WalletHandlerModule.balance;
  private balance_monero = new BigNumber(WalletMoneroHandlerModule.balance).dividedBy(1000000000000) || 0;

  private unlocked_balance_monero = new BigNumber(WalletMoneroHandlerModule.unlockedBalance).dividedBy(1000000000000) || 0;

  private refreshing = false;

  // Wallet stuff
  private seed = "";
  private seed_monero = "";
  private path_monero = ""
  private password_monero = ""

  private xmr_ammount: BigNumber = new BigNumber(0);
  private xmr_priority: number = 1
  private xmr_hash: string = "";
  private last_xmr_tx = "";

  private fiatName = WalletHandlerModule.settings.currency;

  private sending = false;

  @Prop() language!: Language;

   get allTransactions() : any[] {
    //all transaction btx + monero concatanated
        return this.unconfirmedTransactions.concat(this.transactions).sort((tx1, tx2) => tx1.time.getMilliseconds() - tx2.time.getMilliseconds()).reverse();
    }

    get allTransactionsMonero() {
      return this.transactionsMonero.reverse();
    }

  // Transaction details
  private hash = "";
  private btcAmount = "";
  private fee = "";
  private total = "";
  private confirmationAmount = "";
  private receivingAddress = ""
  private sendingMax = false;
  private hex = "";
  private thisLooksRight = false;
  private deleteWallet = false
  private lastTransaction = ""
  private isRestoring = false



  private lang = WalletHandlerModule.currentLanguage;
  private walletModule = WalletHandlerModule

  async mounted() {
    
    // Set the language
    this.lang = this.language;

    this.seed = WalletHandlerModule.wallet.seed;
    let moneroData = await moneroWalletDB.getWallet()
  
    this.seed_monero = moneroData.mnemonic

    this.password_monero = moneroData.password
    this.path_monero = moneroData.path

    if(WalletHandlerModule.restoring) {
      this.isRestoring = true
      await this.syncWallet(false)
      WalletHandlerModule.setRestoring(false)
      this.isRestoring = false
    }

    else {
      await this.syncWallet(true)
      this.timer = setInterval(this.doSmallSync, 600000);
    }

    this.balance_monero = new BigNumber(WalletMoneroHandlerModule.balance).dividedBy(1000000000000);

    this.unlocked_balance_monero = new BigNumber(WalletMoneroHandlerModule.unlockedBalance).dividedBy(1000000000000);

    

  }

  overallFiat() {
    return 
  }

  showSeed() {
    this.seedModal = true;
  }
  hideSeed() {
    this.seedModal = false;
  }

  showSeedMonero() {
    this.seedModalMonero = true;
  }
  hideSeedMonero() {
    this.seedModalMonero = false;
  }


  


  


  // Displays the transaction confirmation modal with all the detaisl
  async showTransaction(details: any[]) {
    this.thisLooksRight = false;
    let transaction: any = details[0];
    this.sendingMax = details[2];
    this.transactionModalShowing = true;
    this.fee = new BigNumber(transaction.getFee())
      .dividedBy(100000000)
      .toString();
    this.btcAmount = details[3]
    this.receivingAddress = details[1];


    this.total = this.sendingMax
      ? new BigNumber(this.btcAmount).minus(this.fee).toString()
      : new BigNumber(this.btcAmount).plus(this.fee).toString();

    // The amount the recipient of the transaction gets
    this.confirmationAmount = this.sendingMax
      ? new BigNumber(this.btcAmount).minus(this.fee).toString()
      : this.btcAmount;

    // The hex of the transaction, used for broadcast later
    this.hex = transaction.extractTransaction().toHex();

    this.hash = transaction.extractTransaction().getHash();

  }

  async showMoneroTransaction(details: any[]) {
    this.thisLooksRight = false;
    this.sendingMax = details[2];
    this.last_xmr_tx = details[0].tx;
    this.transactionModalMoneroShowing = true;
    this.fee = new BigNumber(details[0].fee)
      .dividedBy(1000000000000)
      .toString();
    this.xmr_ammount = details[3];
    
    this.receivingAddress = details[1].substring(0, 19) + '...' + details[1].substring(85, 95);

    let properAmmount = new BigNumber(this.xmr_ammount).multipliedBy(1000000000000);

    this.total = new BigNumber(details[0].fee).plus(properAmmount).dividedBy(1000000000000).toString();
    

    // The amount the recipient of the transaction gets
    this.confirmationAmount = this.sendingMax
      ? new BigNumber(this.xmr_ammount).minus(this.fee).toString()
      : this.xmr_ammount.toString();

    this.xmr_hash = details[0].tx;
    this.transactionModalMoneroShowing = true;
  }

  showMoneroError(details: any[]) {
    this.last_xmr_tx = details[0].tx;
    this.transactionModalMoneroShowing = true;


  }

  // Changes the displayed fiat currency
  async switchCurrency() {
    let settings = WalletHandlerModule.settings;
    this.fiatName = settings.currency;
  }

  switchLanguage() {
    this.lang = WalletHandlerModule.currentLanguage;
    this.$emit('language-changed')
  }

  // Hide the transaction modal
  hideTransaction() {
    this.transactionModalShowing = false;
  }

  showError() {
    this.errorModalShowing = true;
  }
  hideError() {
    this.errorModalShowing = false;
  }

  hideMoneroTransaction() {
    this.transactionModalMoneroShowing = false;
  }

  // Broadcasts the transaction to the Bitcoin network
  async sendTransaction() {
    // Set this so the loading indicator displays
    this.sending = true;

    // Broadcast the transaction
    await WalletHandlerModule.broadcastTransaction(this.hex);

    // Take away the total being spent by this transaction from our balance
    this.balance = this.balance.minus(new BigNumber(this.total));

    // Displays the new transaction on our table
    let tx = new Transaction(this.hash, "-" + (this.sendingMax ? new BigNumber(this.btcAmount).toString() : this.total), 0, new Date(), false)

    this.unconfirmedTransactions.push(tx)

    // We're done sending
    this.sending = false;

    // Get rid of the modal now
    this.hideTransaction();

    // Show the overview tab so the user can see their transaction
    this.currentTab = 'Overview'
  }

  async sendTransactionMonero() {

    this.sending = true;

// Broadcast the transaction

  try {
    await WalletMoneroHandlerModule.sendTransaction(this.last_xmr_tx);
  } catch (e) {
    //TODO handle better error, or display it
    console.log(e)
  }
  finally {
    this.sending = false;
    this.hideMoneroTransaction();
    this.last_xmr_tx = "";
    this.xmr_ammount = new BigNumber(0);
    this.xmr_priority = 1;
    this.receivingAddress = "";
    this.currentTab = 'Overview'
  }

  }

  intlFormater(amount: string) {
    return new Intl.NumberFormat(this.walletModule.settings.language, { style: 'currency', currency: this.walletModule.settings.currency }).format(parseFloat(amount));
  }

  btcToFiat(btc: string): string {
    let fiat = (parseFloat(btc) * WalletHandlerModule.fiatRate).toFixed(2);

    if (isNaN(parseFloat(fiat))) {
      fiat = '0';
    }
    return fiat;

  }

  xmrToFiat(xmr: string): string {
    let fiat = (parseFloat(xmr) * WalletMoneroHandlerModule.fiatRate).toFixed(2);

    if (isNaN(parseFloat(fiat))) {
      fiat = '0';
    }
    return fiat;
  }

  close() {
    this.deleteModal = true
  }

  deleteAndDestroyWallet() {
    this.$emit("close-wallet")
  }

  async doSmallSync() {
    this.syncWallet(true) 
  }

  async syncWallet( smallSync : boolean) {
    if (this.refreshing || this.sending || this.transactionModalShowing) {
      return;
    }

    this.refreshing = true;

    try {
      await WalletHandlerModule.syncWallet(smallSync);
      await WalletMoneroHandlerModule.forceSyncWallet();
      this.transactions = WalletHandlerModule.transactions;
      this.transactionsMonero = WalletMoneroHandlerModule.transactions;
      this.unconfirmedTransactions = WalletHandlerModule.unconfirmedTransactions;
      this.balance = WalletHandlerModule.balance
      this.balance_monero = new BigNumber(WalletMoneroHandlerModule.balance).dividedBy(1000000000000) || new BigNumber(0);
      this.unlocked_balance_monero = new BigNumber(WalletMoneroHandlerModule.unlockedBalance).dividedBy(1000000000000) || new BigNumber(0);
      this.refreshing = false;
    } catch {
      this.refreshing = false;
    }

  }

  beforeDestroy() {
    clearInterval(this.timer!);
  }
}
