export default class Utils {
    static languageBigName(l : string) : string {
        if (l == "en") {
          return 'English'
        }
    
        else if (l == "es") {
          return 'Espaol'
        }
    
        else if (l == "fr") {
          return 'Franais'
        }
        
        else if(l == "ja") {
          return ''
        }

        else if(l == "it") {
          return 'Italiano'
        }
        
        else if(l == "ca") {
           return 'Catalan'
        }

        else if(l == "pt-br") {
          return 'Portugus Brasil'
        }
    
        else {
          return 'English'
        }
      }
    
      static languageShortCode(l : string) : string {
    
        if (l == "English") {
          return 'en'
        }
    
        else if (l == "Espaol") {
          return 'es'
        }
    
        else if (l == "Franais") {
          return 'fr'
        }
        
        else if(l == "") {
          return 'ja'
        }

        else if(l == "Italiano") {
          return 'it'
        }
        
        else if(l == "Catalan") {
           return 'ca'
        }

        else if(l == 'Portugus Brasil') {
          return 'pt-br'
        }
    
        else {
          return 'en'
        }
    
      }
}